* {
    font-family: Helvetica, Arial , sans-serif;
}

body {
    padding: 60px;
}
input{
    color: gray;
    width: 100%;
    font-size: 1.2rem;
}

.card-grid{
    display: flex;   
    flex-direction: row;
    flex-wrap: wrap;
}

.card {
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    margin-right: 10px;
    margin-left: 10px;
    overflow: hidden; /*para que los bordes de las imagenes no se salgan del contenedor*/
}
.card p{
    padding: 5px;
    text-align: center;
}

.card img{
    max-height: 170px;
}
